.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;

  font-family: @font-text;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: @text-color;
}

.copyright__text {
  margin: 0;
}

.copyright__link {
  display: block;

  text-align: right;
}
